* {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}






.darkmode input{
    display: none;
}
.darkmode label{
    position: relative;
    width: 50px;
    height: 26px;
    display: block;
    background: #d9d9d9;
    border-radius: 100px;
    cursor: pointer;
    box-shadow: inset 0px 5px 15px rgba(0,0,0,0.3), inset 0px -5px 15px rgba(255,255,255,0.3);
}

.darkmode label::after{
    content: "";
    position: absolute;
    height: 20px;
    width: 20px;
    background: #f2f2f2;
    border-radius: 100%;
    top: 3px;
    left: 3px;
    transition: 0.5s;
    box-shadow: 0 5px 10px rgba(0,0,0,0.2);

}

input:checked ~ label:after{
    left: 47px;
    transform: translateX(-100%);
    background: linear-gradient(180deg,#777,#3a3a3a);
  }
  input:checked ~ label{
    background: #242424;
  }




